<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process104"></a>

<p><br></p>
<p><span class="rvts17">混乱的水域（</span><span class="rvts50">Troubled Waters</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">主线任务</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:13,Y:-245</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：魔导师西万</span>
<br><span class="rvts23"></span>
<br><span class="rvts41">任务目标：</span>
<br><span class="rvts21">按照西万的要求，探索船体，并触发新事件。</span>
<br><span class="rvts23"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber997.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">在苏醒之后，就可以接到面前守门的那位海务总管的</span><span class="rvts20"></span><span class="rvts21">混乱之水</span><span class="rvts20"></span><span class="rvts21">主线任务，她让去前往船仓另外一头报到。之后到达那里，会在此处遇到叛变的源力魔法术士与船上押运士兵同归于尽的场景。</span>
<br><span class="rvts21">一场魔法大火之后，整个押运船着火，并吸引了海怪。此时需要赶紧在刚才同归于尽场景中的</span><span class="rvts20"></span><span class="rvts21">充公箱</span><span class="rvts20"></span><span class="rvts21">里，拿起武器，进行战斗。其实船上的敌人并不多，会在船上找到几处需要特定钥匙才可打开的门，里面会有一些更好的装备，物品，或配方。接着需要探索大船，船其实一共就三层，地图并不多大。船上的船员几乎全部阵亡，</span><span class="rvts20">6</span><span class="rvts21">名源力者只是暂时的昏迷。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber998.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">然后，来到甲板层，有两个矮人准备使用救生艇逃走。但是处于好意，其中的一个矮人（孩子）想要尽可能的拯救船上所有的幸存者。</span>
<br><span class="rvts21">此时可以选择不去下层拯救幸存者而直接离开该章节。但是直接离开就无法获得拯救幸存者部分的经验值。也可以返回下层船舱，寻找我们在游戏刚开始就遇到的</span><span class="rvts20">6</span><span class="rvts21">名源力者，因为它们是整个《神界：原罪</span><span class="rvts20">2</span><span class="rvts21">》的重要人物。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber999.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">找到那</span><span class="rvts20">6</span><span class="rvts21">名源力者后，接下来便会遇到战斗剧情。其实就是加上角色一共</span><span class="rvts20">7</span><span class="rvts21">个人打几只虚灵虫而已。当战斗结束后，它们</span><span class="rvts20">6</span><span class="rvts21">个人会逃到救生船。</span>
<br><span class="rvts21">如果此时也赶往救生船，则会看到整个押运船被摧毁的景象。但是，如果此时去拯救刚开始出生地里的那个刻薄的海务总管的话，则不会逃到船头的甲板上。</span><span class="rvts20"></span>
<br><span class="rvts21">无论选择怎样的剧情，都会开始下一章。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



